import { grpc } from "grpc-web-client";
import { EchoRequest, EchoResponse } from '../../protoc-gen-grpc-web-client/echo_pb'
import { EchoService } from '../../protoc-gen-grpc-web-client/echo_pb_service'
import { setResult, scheme } from './common';

const run = (endpoint: string, id: string) => {
  setResult(id, '', '');

  const req = new EchoRequest()
  req.setText('grpc-web-client')
  grpc.unary(
    EchoService.Echo,
    {
      request: req,
      host: endpoint,
      onEnd: res => {
        const { status, statusMessage, message } = res;
        if (status === grpc.Code.OK && message) {
          const obj = message.toObject() as EchoResponse.AsObject;
          setResult(id, obj.text, '');
        } else {
          setResult(id, statusMessage, String(status));
        }
      }
    }
  );
}

let grpcWebClientCaseCount = 0;
const grpcWebClientTbody = document.getElementById('grpc-web-client-tbody');
if (grpcWebClientTbody) {
  grpcWebClientCaseCount = grpcWebClientTbody.childElementCount;
}
for (let i = 1; i <= grpcWebClientCaseCount; i++) {
  const id = 'grpc-web-client-case' + i;
  const button = document.getElementById(id + '-run');
  if (button) {
    button.addEventListener('click', () => {
      const endpoint = scheme === 'https:' ? button.getAttribute('data-url-https') : button.getAttribute('data-url');
      if (endpoint) {
        run(endpoint, id);
      }
    });
  }
}
